<template>
  <div class="user">
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div @click="$exportDetail('/export/teachGroupUserExport.do',{})" class="btn-main mr15">导出</div>
        <div @click="showAddDialog" class="btn-main-deepen">添加老师</div>
        <!--<div @click="showBatchDialog()" class="btn-main">批量设置</div>-->
      </div>
      <!--<search-box :searchData="searchData" :table="table"></search-box>-->
      <div class="oz-box">
        <div @click="showAddForm" class="btn-submit btn-add">增加教研组</div>
        <div class="clear"></div>
        <div class="item" v-show="teachGroupStatus && !teachGroup.teachGroupId">
          <div class="left">
            <el-input v-model="teachGroup.teachGroupName" size="small" placeholder="请输入教研组"></el-input>
          </div>
          <div class="right">
            <span @click="addteachGroup" class="oper">保存</span>
            <span @click="teachGroupStatus = false" class="oper">取消</span>
          </div>
        </div>

        <div class="item" v-for="item in depData">
          <div v-show="teachGroupStatus && teachGroup.teachGroupId=== item.id">
            <div class="left">
              <el-input v-model="teachGroup.teachGroupName"
                        size="small" placeholder="请输入教研组"></el-input>
            </div>
            <div class="right">
              <span class="oper" @click="editteachGroup(item)">完成</span>
              <span @click="teachGroupStatus = false" class="oper">取消</span>
            </div>
          </div>
          <div v-show="!(teachGroupStatus && teachGroup.teachGroupId=== item.id)">
            <div class="left">
              <el-checkbox @change="(value) => checkboxChange(value,item)"
                           :value="item.id === table.query.teachGroupId"
              ><span>{{item.name}}</span></el-checkbox>
            </div>
            <div class="right">
              <span @click="showAddForm(item)" class="oper">编辑</span>
              <span @click="removeteachGroup(item)"class="oper">删除</span>
            </div>
          </div>
        </div>
      </div>
      <div class="table-box">
        <resize-table>
        <table class="table">
          <thead>
          <tr>
            <th width="4%"><el-checkbox v-model="checkAll"></el-checkbox></th>
            <th width="9%">姓名</th>
            <th width="10%">所属部门</th>
            <th width="10%">职称</th>
            <th width="8%">年级</th>
            <th width="9%">教研组</th>
            <th width="9%">学科</th>
            <th width="11%">手机号</th>
            <th width="8%">教研组长</th>
            <th width="8%">备课组长</th>
            <th width="8%">操作</th>
          </tr></thead>
          <tbody>
          <tr v-for="(item,key) in table.list">
            <td><el-checkbox v-model="checkboxList[key]"></el-checkbox></td>
            <td>{{item.name}}</td>
            <td>
              <el-tooltip :content="item.orgList.map(e=> e.name).join('，')" placement="top" effect="dark">
                <p class="org-list">{{item.orgList.map(e=> e.name).join('，')}}</p>
              </el-tooltip>
            </td>
            <td>{{$enums.TeacherTitleType.getName(item.title)}}</td>
            <td>
              <el-tooltip :content="item.gradeName" placement="top" effect="dark">
                <p class="org-list">{{item.gradeName}}</p>
              </el-tooltip>
            </td>
            <td>
              <el-tooltip :content="item.teachGroupList.map(e=> e.name).join('，')" placement="top" effect="dark">
                <p class="org-list">{{item.teachGroupList.map(e=> e.name).join('，')}}</p>
              </el-tooltip>
            </td>
            <td>
              <el-tooltip :content="item.courseList.map(e=> e.courseName).join('，')" placement="top" effect="dark">
                <p class="org-list">{{item.courseList.map(e=> e.courseName).join('，')}}</p>
              </el-tooltip>
            </td>
            <td>{{item.phone}}</td>
            <td>{{$enums.YesNoStatus.getName(item.isTeachGroupLeader)}}</td>
            <td>{{$enums.YesNoStatus.getName(item.isLessonPrepareLeader)}}</td>
            <td>
              <span v-show="item.gradeName" @click="showSingleDialog(item)" class="oper">设置</span>
              <span @click="remove(item)" class="oper">删除</span>
            </td>
          </tr></tbody>
        </table>
        </resize-table>
        <no-data v-show="!table.noData"></no-data>
        <paging :data.sync="table"></paging>
      </div>
      <!--添加老师弹窗-->
      <el-dialog width="640px" title="添加老师" class="dialog" :visible.sync="teacherDialog">
        <div class="item">
          <span class="title">当前教研组</span>
          <el-input :value="addForm.teachGroupName" disabled class="input"></el-input>
        </div>
        <div class="item">
          <span class="title imp">科目</span>
          <el-select v-model="addForm.courseId" default-first-option class="input" clearable filterable>
            <el-option
                v-for="item in courseList"
                :value="item.id" :label="item.name" :key="item.id"
            ></el-option>
          </el-select>
        </div>
        <div class="item">
          <span class="title imp">年级</span>
          <el-select default-first-option v-model="addForm.gradeId" class="input" clearable filterable>
            <el-option
                v-for="item in gradeList"
                :value="item.id" :label="item.name" :key="item.id"
            ></el-option>
          </el-select>
        </div>
        <el-input class="search-input" @keyup.enter.native="$search(undistributed)" v-model="undistributed.query.keywords" placeholder="姓名/身份证号">
          <el-button @click="$search(undistributed)" slot="append" icon="el-icon-search"></el-button>
        </el-input>
        <resize-table>
        <table class="table teacher-list">
          <thead>
          <tr>
            <th width="10%">选择</th>
            <th width="16%">姓名</th>
            <th width="10%">姓别</th>
            <th width="34%">身份证号码</th>
            <th width="30%">职务</th>
          </tr></thead>
          <tbody>
          <tr v-if="teacherDialog" v-for="(item,key) in undistributed.list">
            <td><el-checkbox @change="item.status = !item.status"></el-checkbox></td>
            <td>{{item.name}}</td>
            <td>{{$enums.Sex.getName(item.sex)}}</td>
            <td>{{item.idCard}}</td>
            <td>{{item.position || '--'}}</td>
          </tr></tbody>
        </table>
        </resize-table>
        <no-data v-show="!undistributed.noData"></no-data>
        <paging :data.sync="undistributed"></paging>
        <div class="btns mt25">
          <div @click="addTeacher" class="btn-submit mr15">确认</div>
          <div @click="teacherDialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
      <!--设置 已废弃-->
      <el-dialog width="540px" title="批量设置" class="dialog batch-dialog" :visible.sync="batchDialog">
        <div class="item">
          <span class="title imp">是否教研组长</span>
          <el-radio border :key="key" v-for="(item,key) in $enums.YesNoStatus.list"
                    v-model="batchForm.isTeachGroupLeader" :label="item.value">
            &nbsp;&nbsp;{{item.label}}&nbsp;&nbsp;
          </el-radio>
        </div>
        <div class="item">
          <span class="title imp">是否备课组长</span>
          <el-radio border :key="key" v-for="(item,key) in $enums.YesNoStatus.list"
                    v-model="batchForm.isLessonPrepareLeader" :label="item.value">
            &nbsp;&nbsp;{{item.label}}&nbsp;&nbsp;
          </el-radio>
        </div>
        <div class="btns">
          <div @click="batchFun" class="btn-submit btn-set-batch mr15">确认</div>
          <div @click="batchDialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
      <!--单个设置-->
      <el-dialog width="540px" title="设置" class="dialog" :visible.sync="singleDialog">
        <div class="item">
          <span class="title">教授科目</span>
          <!--<el-input disabled :value="singleForm.courseName" class="input"></el-input>-->
          <!--<el-select clearable v-model="singleForm.teachGroupId">-->
            <!--<el-option-->
                <!--v-for="item in courseList"-->
                <!--:value="item.id" :label="item.name" :key="item.id"-->
            <!--&gt;</el-option>-->
          <!--</el-select>-->
          <el-select v-model="singleForm.courseIds" multiple class="input" clearable filterable>
            <el-option
                v-for="item in courseList"
                :value="item.id" :label="item.name" :key="item.id"
            ></el-option>
          </el-select>
        </div>
        <div class="item">
          <span class="title">是否教研组长</span>
          <el-radio border :key="key" v-for="(item,key) in $enums.YesNoStatus.list"
                    v-model="singleForm.isTeachGroupLeader" :label="item.value">
            &nbsp;&nbsp;{{item.label}}&nbsp;&nbsp;
          </el-radio>
        </div>
        <div class="item">
          <span class="title">是否备课组长</span>
          <el-radio border :key="key" v-for="(item,key) in $enums.YesNoStatus.list"
                    v-model="singleForm.isLessonPrepareLeader" :label="item.value">
            &nbsp;&nbsp;{{item.label}}&nbsp;&nbsp;
          </el-radio>
        </div>
        <div v-if="singleForm.isLessonPrepareLeader === 'YES'" class="item">
          <span class="title imp">年级</span>
          <el-select class="input" multiple v-model="singleForm.gradeIds">
            <el-option :key="key" v-for="(item,key) in gradeList"
                       :value="item.id" :label="item.name"
            ></el-option>
          </el-select>
        </div>
        <div class="btns">
          <div @click="singleFun" class="btn-submit btn-set-single mr15">确认</div>
          <div @click="singleDialog = false" class="btn-clear">取消</div>
        </div>
      </el-dialog>
    </main-part>
  </div>

</template>
<script>
  import mixins from '../../../assets/js/mixins'
  export default {
    mixins: [mixins.$exportDetail],
    data(vm) {
      return {
        nav: [{name: "教研组管理", path: ""}],
        searchData: [{type: 'input', placeholder: '用户名'}],
        table: {
          url: '/teachGroup/queryTeachGroupUserPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            teachGroupId: '',
            keywords: ''
          }
        },
        // 年级列表
        gradeList: [],
        // 选择的部门
        selectedDep: [],
        // 表格多选框
        checkboxList:[false],
        // 教研组树
        depData: [],
        // 是否为编辑教研组状态
        teachGroupStatus: false,
        // 新增修改教研组
        teachGroup: {
          teachGroupId: null,
          teachGroupName: ''
        },
        // 添加老师弹窗是否展现
        teacherDialog: false,
        // 教研组下的科目列表
        courseList: [],
        // 教研组下未分配的老师数据
        undistributed: {
          url: '/teachGroup/queryTeachGroupUnAllocateUserPage.do',
          el: '.teacher-list',
          total: 0,
          noData: true,
          list: [],
          query:{
            id: null
          }
        },
        // 添加老师表单数据
        addForm:{
          teachGroupId: null,
          gradeId: null,
          courseId: null,
          userIds: []
        },
        // 批量设置弹窗
        batchDialog: false,
        // 批量设置表单数据
        batchForm:{
          teachGroupId: this.teachGroupId,
          userIds: [],
          isTeachGroupLeader: 'YES',
          isLessonPrepareLeader: 'YES',
        },
        // 所有在读的班级
        classList: [],
        // 单个设置弹窗
        singleDialog: false,
        // 单个设置表单数据
        singleForm:{
          id: '',
          courseIds: [],
          gradeIds: [],
          courseName: '',
          gradeName: '',
          isTeachGroupLeader: '',
          isLessonPrepareLeader: ''
        }
      }
    },
    watch:{
      'table.list':function () {
        this.checkboxList = new Array(this.table.list.length).fill(false)
      },
      'undistributed.list': function () {
        this.undistributed.list.forEach(e=>{
          e.status = false
        })
      }
    },
    computed: {
      checkAll: {
        get: function () {
          if(this.checkboxList.length > 0){
            return this.checkboxList.every(item => item)
          }else{
            return false
          }
        },
        set: function (val) {
          this.checkboxList = new Array(this.table.list.length).fill(val)
        }
      },
      teachGroupId: function () {
        return this.table.query.teachGroupId
      }
    },
    methods: {
      // 展示新增教研组表单
      showAddForm(item){
        if(item){
          this.teachGroup = {
            teachGroupId: item.id,
            teachGroupName: item.name
          }
        }else{
          this.teachGroup = {
            teachGroupId: null,
            teachGroupName: ''
          }
        }
        this.teachGroupStatus = true
      },
      // 新增教研组
      async addteachGroup(){
        await this.$ajax({
          url: '/teachGroup/addTeachGroup.do',
          query:{
            teachGroupName: this.teachGroup.teachGroupName
          }
        })
        this.$remind('教研组新增成功')
        this.getAllDep()
        this.teachGroupStatus = false
      },
      // 获取年级列表
      async getGradeList() {
        this.gradeList = await this.$ajax({
          url: '/grade/queryGradeList.do',
        })
      },
      // 修改教研组
      async editteachGroup(){
        await this.$ajax({
          url: '/teachGroup/updateTeachGroup.do',
          query: this.teachGroup
        })
        this.$remind('教研组修改成功')
        this.getAllDep()
        this.teachGroupStatus = false
      },
      // 展示添加老师弹窗
      async showAddDialog(){
        this.teacherDialog = true
        this.addForm.courseId = null
        this.addForm.gradeId = null
        this.addForm.userIds = []
        // 获取教研组下的科目
        this.courseList = await this.$ajax({
          url: '/teachGroup/getTeachGroupCourseList.do',
          query:{
            id: this.table.query.teachGroupId
          }
        })
        // 获取教研组下未分配的老师列表
        this.$search(this.undistributed)
      },
      // 获取所有的教研组
      async getAllDep(){
        let res  = await this.$ajax({
          url: '/teachGroup/queryTeachGroupList.do',
        })
        this.depData = res
        this.table.query.teachGroupId =
        this.undistributed.query.id = res[0].id
        this.addForm.teachGroupName = res[0].name
        this.addForm.teachGroupId = res[0].id
      },
      // 删除教研组
      async removeteachGroup(item){
        await this.$confirm(`您确认删除【${item.name}】吗？`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/teachGroup/deleteTeachGroup.do',
          query:{
            id: item.id
          }
        })
        this.$remind('教研组删除成功')
        this.getAllDep()
        this.$search(this.table)
      },
      // 删除教研组成员
      async remove(item){
        await this.$confirm(`您确认删除【${item.name}】吗？`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        await this.$ajax({
          url: '/teachGroup/deleteTeachGroupUser.do',
          query:{
            userId: item.userId,
            teachGroupId: this.teachGroupId
          }
        })
        this.$remind('删除成功')
        this.$search(this.table)
      },
      // 选择教研组
      async checkboxChange(val,item){
        this.table.query.teachGroupId = item.id
        this.undistributed.query.id = item.id
        this.addForm.teachGroupId = item.id
        this.addForm.teachGroupName = item.name
        this.$search(this.table)
      },
      // 添加老师
      async addTeacher(){
        if(!this.addForm.courseId){
          this.$message.warning('请选择科目')
          return
        }
        if(!this.addForm.gradeId){
          this.$message.warning('请选择年级')
          return
        }
        // 获取选择的老师ID
        this.addForm.userIds = []
        this.undistributed.list.forEach((e,i)=>{
          if(e.status){
            this.addForm.userIds.push(this.undistributed.list[i].id)
          }
        })
        await this.$ajax({
          url: '/teachGroup/addTeachGroupUser.do',
          query: this.addForm
        })
        this.teacherDialog = false
        this.$remind('添加成功')
        this.$search(this.table)
      },
      // 批量设置弹窗
      showBatchDialog(){
        let status = this.checkboxList.some(e => e)
        if(!status){
          this.$message.warning('请先选择老师')
          return
        }
        this.batchDialog = true
      },
      // 批量删除
      async removeBatch(){
        let status = this.checkboxList.some(e => e)
        if(!status){
          this.$message.warning('请先选择用户')
          return
        }
        await this.$confirm(`您确认删除选中的用户吗？`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        })
        let arr = []
        this.checkboxList.forEach((e,i)=>{
          if(e){
            arr.push(this.table.list[i].userId)
          }
        })
        await this.$ajax({
          url: '/user/deleteUserBatch.do',
          query:{
            userIds: arr
          }
        })
        this.$remind('禁用成功')
        this.$search(this.table)
      },
      // 批量设置教研组成员
      async batchFun(){
        this.batchForm.userIds = []
        this.checkboxList.forEach((e,i)=>{
          if(e){
            this.batchForm.userIds.push(this.table.list[i].userId)
          }
        })
        this.batchForm.teachGroupId =  this.teachGroupId
        await this.$ajax({
          url: '/teachGroup/updateTeachGroupUserBatch.do',
          el: '.btn-set-batch',
          query: this.batchForm
        })
        this.$remind('设置成功')
        this.batchDialog = true
        this.$search(this.table)

      },
      // 展示单个设置弹窗
      async showSingleDialog(item){
        // 获取教研组下的科目
        this.courseList = await this.$ajax({
          url: '/teachGroupCourse/queryCourseList.do',
          query:{
            // id: this.table.query.teachGroupId
          }
        })
        this.singleForm = {
          id: item.userId,
          teachGroupId: item.courseList.length ? item.courseList[0].id : '',
          courseName: item.courseList.map(e=> e.courseName).join('，'),
          courseIds: item.courseList.map(e=> e.courseId),
          // gradeIds: item.gradeIds,
          gradeIds: item.gradeIds,
          isTeachGroupLeader: item.isTeachGroupLeader,
          isLessonPrepareLeader: item.isLessonPrepareLeader
        }
        this.singleDialog = true
      },
      // 单个设置
      async singleFun(){
        if(this.singleForm.isLessonPrepareLeader === 'YES' && this.singleForm.gradeIds.length <=0){
          this.$message.warning('请选择年级')
          return
        }
        await this.$ajax({
          url: '/teachGroup/updateTeachGroupUser.do',
          el: '.btn-set-single',
          query:{
            gradeIds: this.singleForm.gradeIds,
            userId: this.singleForm.id,
            teachGroupId: this.teachGroupId,
            courseIds: this.singleForm.courseIds,
            isTeachGroupLeader: this.singleForm.isTeachGroupLeader,
            isLessonPrepareLeader: this.singleForm.isLessonPrepareLeader
          }
        })
        this.$remind('设置成功')
        this.$search(this.table)
        this.singleDialog = false
      }
    },
    async mounted(){
      await this.getAllDep()
      this.$search(this.table)
      // 取消选择所有的多选框
      this.checkboxList = new Array(this.table.list.length).fill(false)
      this.getGradeList()
    }
  }
</script>
<style lang="sass" scoped>
  .user
    /deep/ .el-input-group__append, .el-input-group__prepend
      background: #fff
    .org-list
      @extend %text-overflow
    .oz-box
      width: 260px
      height: calc(100vh - 140px)
      overflow-y: auto
      border: 1px solid $border
      background: #fff
      border-radius: 4px
      float: left

      .btn-add
        float: right
        margin-top: 10px
        margin-right: 10px
        margin-bottom: 10px
      .item
        line-height: 48px
        border-top: 1px solid $border
        padding: 0 10px
        .left
          width: 67%
          padding-right: 20px
          display: inline-block
        .right
          width: 33%
          display: inline-block
          .oper
            cursor: pointer
            color: $main-c
            margin: 0 5px
            @include btn-opacity
      .item:last-child
        border-bottom: 1px solid $border
    .dialog
      .title
        width: 90px
        line-height: 40px
        vertical-align: top
        display: inline-block
      .search-input
        margin-bottom: 15px
        width: 260px
      .imp:before
        content: '*'
        color: $error-c
        margin-right: 4px
      .input
        width: calc(100% - 90px)
      .item
        margin-bottom: 20px
      .btns
        text-align: center
    .batch-dialog
      .title
        width: 110px
    .table-box
      margin-left: 15px
      width: calc(100% - 275px)
      float: left
</style>
